

import HeaderBack from './HeaderBack'

import Scroller from 'vux/src/components/scroller/index.vue'
import LoadMore from 'vux/src/components/load-more/index.vue'


export default {
name: 'scrollTemplate',
components: {
myHeader: HeaderBack,
Scroller,
LoadMore,
},
data () {
return {
header: {
HBL_icon:"icon_back",
HBC_text:"商品分类",
HBR_icon: " ",
},
pullup:{
config: {
content: '上拉刷新',
pullUpHeight: 40,
height: 40,
autoRefresh: false,
downContent: '释放加载',
upContent: '上拉刷新',
loadingContent: 'Loading...',
clsPrefix: 'xs-plugin-pullup-'
}

},
onFetching: false,
isAll: false,
isLoadMore: true,
bottomCount: 20,
}
},
computed: {
getHeight() {

}
},
methods: {

onScrollBottom () {







},
onScroll (pos) {

},
pullupLoading () {
var self = this;
if (!self.onFetching) {
self.onFetching = true;
self.isLoadMore = false;
setTimeout(() => {
self.bottomCount += 10
self.$nextTick(() => {
self.$refs.scrollerBottom.reset()
self.$refs.scrollerBottom.donePullup();


});
self.onFetching = false;
self.isLoadMore = true;


}, 2000);
setTimeout(()=>{
self.isAll = true;
},3000)
}
}




},
mounted() {

setTimeout(function () {
comTools.setHeight(1.75,".classify>div:nth-child(2)");
},0)

}
}



